<script>
import Swiper from 'swiper';
// // @ is an alias to /src
import company from '@/components/company.vue'

import {homePageInfo} from '@/request/api.js'

export default {
  name: 'home',
  components: {
    company
  },
  data() {
    return {
      page_info: {},
      // 当前轮播index
      current_swiper_index: 0,
      // 更多菜单
      show_more_menu: false,
      is_show_modal: false
    }
  },
  created () {
    this.homePageInfo();
  },
  mounted () {
    let that = this;
    // 监听动画结束再隐藏
    this.$refs.modal.addEventListener('webkitAnimationEnd',function () {
      if(!that.show_more_menu) {
        that.is_show_modal = false;
      }
    })
          
  },
  updated () {
    let that = this;
    this.$nextTick(function () {
      // 实例轮播
      new Swiper ('.swiper-container', {
        loop: false,
        // 2s自动播放
        autoplay: {
          delay: 2000
        },
        observer:true,//修改swiper自己或子元素时，自动初始化swiper 
        observeParents:false,//修改swiper的父元素时，自动初始化swiper
        on:{
          transitionEnd: function(){
            that.current_swiper_index = this.realIndex;
          }
        }
      })
    });
    
  },
  methods: {
    showMoreMenu () {
      this.show_more_menu = !this.show_more_menu;
      if(this.show_more_menu) {
        this.is_show_modal = true;
      }
    },
    homePageInfo () {
      let that = this;
      let data = {};
      homePageInfo(data).then(res => {
        if(res.code == 200) {
          that.page_info = res.data;
        }
      })
    },
    toFeedback () {
      this.$router.push({
          path: '/feedback'
      })
    },
    toContact () {
      this.$router.push({
          path: '/contact'
      })
    },
    toAboutus () {
      this.$router.push({
          path: '/aboutus'
      })
    },
    toCategory () {
      this.$router.push({
          path: 'category'
      })
    },
    toProject () {
      this.$router.push({
          path: 'project'
      })
    },
    toIntroduce (type, data_id) {
      // type: 1.产品 2.实训室 3.案例 4.视频
      this.$router.push({
          path: '/introduce?type=' + type + '&id=' + data_id
      })
    }
  }
}
</script>

