import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import 'swiper/dist/css/swiper.css'

Vue.config.productionTip = false

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')


// 应对ios 微信底部白条
window.onresize = function () {
  let innerHeight = window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight;
  document.getElementById('app').style.height = innerHeight;
}

router.afterEach((to, from) => {
  let tab_choosen;
  switch(to.path) {
    case '/main/home':
      tab_choosen = 0;
      break;
    case '/main/category':
      tab_choosen = 1;
      break;
    case '/main/project':
      tab_choosen = 2;
      break;
    case '/main/videos':
      tab_choosen = 3;
      break;
    case '/main/camera':
      tab_choosen = 4;
      break;
  }
  store.commit('setTabChoosen',tab_choosen);
})